.chord-diagram{
    text-align: left;;
    display: flex;
    flex-direction: column;
    align-items: center;}
.string{
    display:flex;
    list-style-type: none;
    justify-content: space-evenly;
}
#fret0{
    width:0%;
}
.Fret{
    margin-top: -18px;
    border: 10px;
    border-top:  10px double black;
    border-left: solid black;
    border-right: solid black;
    border-bottom:double black;
    width: 7%;
}

.Fret0, .Fret1, .Fret2, .Fret3, .Fret4, .Fret5{
    margin-top: -18px;
    border: 10px;
    border-top:  10px double black;
    border-left: solid black;
    border-right: solid black;
    border-bottom:double black;
    width: 7%;
}

.Fret5{
    border-left:none;
    border-right:none;
    border-bottom:none;
}
.string{
    height:8%;
}

#String5::before{
    content:"E";
    font-weight:bolder;
    font-size:1.75rem;

    position:relative;
    top:-100%;
}
#String4::before{
    content:"A";
    font-weight:bolder;
    font-size:1.75rem;

    position:relative;
    top:-100%;
}
#String3::before{
    content:"D";
    font-weight:bolder;
    font-size:1.75rem;
    position:relative;
    top:-100%;
}
#String2::before{
    content:"G";
    font-weight:bolder;
    font-size:1.75rem;

    position:relative;
    top:-100%;
}
#String1::before{
    content:"B";
    font-weight:bolder;
    font-size:1.75rem;

    position:relative;
    top:-100%;
}
#String0::before{
    content:"E";
    font-weight:bolder;
    font-size:1.75rem;

    position:relative;
    top:-100%;
}


