.active{
    background: black;
    height: 80%;
    border-radius: 100%;
    width: 25%;
    margin: 0px auto;
    position: relative;
    top: -60%;

}
.fretBoard{
    height: 92%;
    width: 77%;
    margin-left: 10%;
    
}
.fretactive{
    background-color:black;
    border-radius:100%
}



.diagram{
    height: 50vh;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 77% 100%;
}


